
<template>

  <div id="app" class="app">
 
    <div class="header">
     
      <nav>
        <button v-if="is_auth" v-on:click="loadHome">Inicio</button>
       
      </nav>
      
      <h2>SISTEMA DE GESTIÓN DE NÓMINA</h2>
      <nav>
       
        <button v-if="is_auth" v-on:click="logOut">Cerrar sesión</button>
      
      </nav>
    </div>

    <div class="main-component">
      <router-view v-on:completedLogIn="completedLogIn" v-on:logOut="logOut">
      </router-view>
    </div>

    <div class="footer">
      <h2>Misión TIC 2022 - MicroAQTech S.A. </h2>
      
    </div>
  </div>
</template>

<script>
export default {
  name: "App",

  data: function () {
    return {
      is_auth: false,
    };
  },

  components: {},

  methods: {
    verifyAuth: function () {
      this.is_auth = localStorage.getItem("isAuth") || false;
      if (this.is_auth == false) this.$router.push({ name: "logIn" });
      else this.$router.push({ name: "home" });
      if (localStorage.getItem("username") == "admin") {
        localStorage.setItem("is_superuser", true);
      }
    },

    loadLogIn: function () {
      this.$router.push({ name: "logIn" });
    },

    completedLogIn: function (data) {
      localStorage.setItem("isAuth", true);
      localStorage.setItem("username", data.username);
      localStorage.setItem("token_access", data.token_access);
      localStorage.setItem("token_refresh", data.token_refresh);
      alert("Autenticación Exitosa");
      this.verifyAuth();
    },

    completedSignUp: function (data) {
      alert("Registro Exitoso");
      this.completedLogIn(data);
    },

    loadHome: function () {
      this.$router.push({ name: "home" });
    },

    logOut: function () {
      localStorage.clear();
      alert("Sesión cerrada");
      this.verifyAuth();
    },
  },

  created: function () {
    this.verifyAuth();
  },
};

window.__VUE_DEVTOOLS_HOST__ = 'localhost' // default: localhost
window.__VUE_DEVTOOLS_PORT__ = '8081' // default: 8098


</script>

